.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-box {
  position: relative;
  max-width: 100%;
  max-height: 80%;  
  overflow: hidden;
}

.modal-content {
  margin: 0;
  justify-content: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}

.images-container {
  display: flex; /* Flexbox로 정렬 */
  justify-content: center; /* 중앙 정렬 */
  align-items: center; /* 세로 정렬 */
  margin: 10px 0;
}

.modal-image {
  width: 500px; /* 이미지를 컨테이너에 맞게 조정 */
  max-height: 1000px; /* 최대 높이를 설정하여 이미지 비율 유지 */
  object-fit: cover; /* 비율 유지 */
}

.close-btn,
.prev-btn,
.next-btn {
  position: absolute;
  background: none;
  border: none;
  color: white;
  font-size: 60px;
  cursor: pointer;
  z-index: 10;
}

.close-btn {
  top: 10px;
  right: 10px;
}

.prev-btn {
  left: 100px;
  top: 50%;
  transform: translateY(-50%);
}

.next-btn {
  right: 100px;
  top: 50%;
  transform: translateY(-50%);
}

/* 모바일 대응 */
@media (max-width: 1024px) {
  .modal-image {
    width: 90%; /* 1024px 이하에서 이미지 너비 조정 */
    height: auto; /* 비율 유지 */
  }
}

@media (max-width: 768px) {
  .modal-image {
    width: 100%; /* 모바일에서는 너비를 100%로 설정 */
    height: auto; /* 비율 유지 */
    max-height: 400px; /* 최대 높이를 설정하여 과도한 크기 방지 */
  }
  
  .images-container {
    flex-direction: column; /* 세로 방향으로 이미지 정렬 */
    width: 100%; /* 전체 너비 사용 */
    align-items: center; /* 중앙 정렬 */
    gap: 10px; /* 이미지 간격 조정 */
  }

  .close-btn {
    font-size: 30px; /* 모바일에서 닫기 버튼 크기 감소 */
    top: 10px; /* 모바일에서는 위쪽 간격 줄임 */
    right: 10px; /* 모바일에서는 오른쪽 간격 줄임 */
  }

  .prev-btn, .next-btn {
    width: 5px;
    position: absolute; /*  절대 위치로 변경 */
    top: 50%; /* 세로 중앙 배치 */
    transform: translateY(-50%); /* 세로 중앙 정렬 */
    font-size: 30px; /* 버튼 크기 */
  }
  
  .prev-btn {
    left: 5%; /* 왼쪽 위치 */
  }
  
  .next-btn {
    right: 5%; /* 오른쪽 위치 */
  }
}

@media (max-width: 480px) {
  .modal-image {
    width: 100%; /* 작은 화면에서 이미지 너비 100% */
    max-height: 300px; /* 최대 높이를 줄여서 보기 좋게 설정 */
  }


}